.block-help p {
  margin-bottom: 0 !important;
  font-size: 1.4rem;
  line-height: 1.1;
}

.layout-builder-form .form-actions {
  margin: 20px 0;
}

.layout-builder-form .form-actions input.button {
  margin-left: 0;
  padding: 8px 17px;
  font-size: 1.7rem;
}

.layout-builder-form .form-actions .form-item-toggle-content-preview {
  display: none;
}

.layout-builder-form .messages {
  font-size: 1.4rem;
  line-height: 1.1;
}

.layout-builder-form .vertical-tabs .vertical-tabs__menu {
  font-size: 1.6rem;
  line-height: 1.1;
}

.layout-builder-form .vertical-tabs details > .details-wrapper {
  padding: 0 1.5rem;
}

.layout-builder .layout-builder__link--add, .layout-builder .layout-builder__link--configure {
  color: palette(blue);
  font-size: 1.4rem;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
}

.layout-builder {
  padding: 1em 1em 0.5em;
  font-size: 1.5rem;
  border: 2px solid #f5f5f5 !important;
}

.layout-builder .layout-builder__section {
  position: relative;
}

.layout-builder .layout-builder-block {
  background-color: transparent;
}

.layout-builder .layout-builder__link--add {
  display: inline-block;
  text-align: left;
  line-height: 1.1;
  padding: 8px 10px 8px 24px;
  margin: 0 10px;
  background-position: left 5px center;
  background-color: transparent;
}

.layout-builder .layout-builder__link--add:hover {
  color: palette(white);
  background-color: #257cc7;
  background-image: url(/core/misc/icons/bebebe/plus.svg);
}

.layout-builder .layout-builder__add-section {
  padding: 10px;
  margin-bottom: 20px;
  opacity: 0.4;
}

.layout-builder .layout-builder__link--remove {
  position: absolute;
  top: 0;
  right: -8px;
  width: 28px;
  height: 29px;
  padding: 6px;
  text-indent: 9999px;
  border: none;
  border-radius: inherit;
  background-color: #eff6fc;
  background-image: url(/core/misc/icons/787878/ex.svg);
}

.layout-builder .layout-builder__link--remove:hover {
  background-color: palette(red);
  background-image: url(/core/misc/icons/ffffff/ex.svg);
}

.layout-builder .layout-builder__link--configure {
  display: inline-block;
  padding: 2px 10px 2px 10px;
  margin-right: 32px;
  margin-bottom: 2px;
  background-color: #eff6fc;
}

.layout-builder .layout-builder__link--configure:hover {
  background-color: #257cc7;
  color: palette(white);
}

.layout-builder-configure-block .form-item-settings-label-display {
  display: none;
}

.layout-builder-configure-block label {
  margin-bottom: 3px;
}

.layout-builder-configure-block .description {
  margin-top: -15px;
  margin-bottom: 8px;
  font-size: 1.4rem;
  line-height: 1.3;
  font-style: italic;
}

.layout-builder-configure-block .field-multiple-table {
  table-layout: initial;
}

.layout-builder-configure-block .field-multiple-table .tabledrag-handle {
  height: 45px;
  overflow: visible;
  line-height: 30px;
  background-color: var(--color-light);
}

.layout-builder-configure-block .field-multiple-table .tabledrag-handle .handle {
  width: 22px;
  height: 22px;
  background-image: url(/core/misc/icons/000000/move.svg);
}

.layout-builder-configure-block .field-multiple-table .clearfix {
  text-align: right;
}

.layout-builder-configure-block input.button {
  margin-left: 0;
  padding: 8px 17px;
  font-size: 1.7rem;
  text-align: right;
}

.ui-widget input,
.ui-widget select,
.ui-widget textarea,
.ui-widget button {
  font-size: 1.8rem;
}

.quickedit-field.quickedit-editable,
.quickedit-field .quickedit-editable {
  box-shadow: none;
}

.ui-dialog .form-text {
  border-radius: 3px;
  border: 1px solid palette(grey, lightest);
}

.ui-dialog .field--type-string::after {
  content: none;
}
